.signup_container {
  width: 90%;
  height: 95%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  color: white;
  font-family: poppins;
  background-color: rgb(65, 105, 225);
  box-shadow: 0 0 20px 5px rgba(65, 105, 225, 0.5);
  overflow: auto;

  /*
  transform: translateY(-150%);
  transition: all 2s ease-in;
  */
}

.signup_logo {
  font-size: 4rem;
  margin-bottom: 0px;
}

.signup_input {
  width: 15rem;
  height: 2rem;
  border: 0;
  color: royalblue;
  font-size: 1rem;
  font-family: poppins;
  padding: 10px;
  margin: 10px 0 10px 0;
  border-radius: 2px;
  outline-style: none;
}

.signup_input:focus {
  box-shadow: 0 0 10px 3px rgba(0, 247, 255, 0.4);
}

.signup_hint {
  width: 15rem;
  min-height: max-content;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 2px;
  padding: 10px;
  font-size: 0.7rem;
  margin: 5px 0 5px 0;
  /*color: rgb(207, 6, 6);*/
}

.signup_textarea {
  width: 15rem;
  min-height: 5rem;
  background-color: "white";
  font-family: poppins;
  color: royalblue;
  border-radius: 2px;
  padding: 10px;
  font-size: 0.8rem;
  margin: 5px 0 5px 0;
  border: 0;
  outline-style: none;
  resize: none;
}

.signup_textarea:focus {
  box-shadow: 0 0 10px 3px rgba(0, 247, 255, 0.4);
}

.signup_button {
  width: 5rem;
  height: 2rem;

  /*width: 8%;
  height: 7%;*/
  font-family: poppins;
  font-size: 1rem;
  background-color: rgb(235, 232, 232);
  border: 0;
  color: royalblue;
  outline-style: none;

  margin: 5px 0 5px 0;
}

.signup_button:hover {
  cursor: pointer;
  background-color: rgb(231, 230, 230);
}

.signup_button:active {
  background-color: rgb(201, 200, 200);
}

.signup_login_btn {
  cursor: pointer;
  font-weight: 300;
}

.signup_login_btn:hover {
  color: rgb(235, 232, 232);
}

.signup_login_btn:active {
  color: rgb(221, 218, 218);
}
