.account_container {
  width: 95%;
  height: 95%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  font-family: poppins;
  background-color: rgb(65, 105, 225);
  box-shadow: 0 0 20px 5px rgba(65, 105, 225, 0.5);
}

.title_label {
  margin: 10px;
}

.account_btn {
  width: 4.5rem;
  height: 2rem;

  font-family: poppins;
  font-size: 1rem;
  background-color: rgb(235, 232, 232);
  border: 0;
  color: royalblue;
  outline-style: none;
  margin: 10px 10px;

  align-self: flex-start;
  justify-content: flex-start;
}

.account_btn:hover {
  cursor: pointer;
  background-color: rgb(231, 230, 230);
}

.account_btn:active {
  background-color: rgb(201, 200, 200);
}
