.login_container {
  width: 90%;
  height: 95%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  font-family: poppins;
  background-color: rgb(65, 105, 225);
  box-shadow: 0 0 20px 5px rgba(65, 105, 225, 0.5);
  /*
  transform: translateY(-150%);
  transition: all 2s ease-in;
  */
}

.login_logo {
  font-size: 5rem;
  margin-bottom: 50px;
}

.login_input {
  /* width: 25%;
  height: 5%;*/
  width: 14rem;
  height: 2rem;
  background-color: transparent;
  border: 0;
  border-bottom: white 2px solid;
  color: white;
  font-size: 1rem;
  font-weight: 300;
  padding: 10px;
  margin: 10px 0 10px 0;
  font-family: poppins;
  outline-style: none;
}

.login_hint {
  width: 14rem;
  min-height: max-content;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 2px;
  padding: 10px;
  font-size: 0.8rem;
  margin: 5px 0 5px 0;
  /*color: rgb(207, 6, 6);*/
}

.login_button {
  width: 4.5rem;
  height: 2rem;

  /*width: 8%;
  height: 7%;*/
  font-family: poppins;
  font-size: 1rem;
  background-color: rgb(235, 232, 232);
  border: 0;
  color: royalblue;
  outline-style: none;

  margin-top: 20px;
}

.login_button:hover {
  cursor: pointer;
  background-color: rgb(231, 230, 230);
}

.login_button:active {
  background-color: rgb(201, 200, 200);
}

.login_sign_btn {
  margin-top: 20px;
  cursor: pointer;
  font-weight: 300;
}

.login_sign_btn:hover {
  color: rgb(235, 232, 232);
}

.login_sign_btn:active {
  color: rgb(221, 218, 218);
}
