.infofield_info {
  padding: 5px 20px;
  border-radius: 4px;
  cursor: pointer;
  color: white;
  font-family: poppins;
  font-size: 1.2rem;
}

.infofield_info:hover {
  background-color: rgba(65, 225, 225, 0.4);
}

.infofield_edit_backdrop {
  position: absolute;
  top: 0;
  left: 0;

  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.1);
  font-family: poppins;
  background-color: rgba(65, 105, 225, 0.4);
}

.infofield_edit_main {
  width: 18rem;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(65, 225, 225, 0.5);
  border-radius: 5px;
  padding: 10px;
}

.infofield_edit_input {
  width: 15rem;
  height: 5rem;
  border: 0;
  color: royalblue;
  font-size: 1rem;
  font-family: poppins;
  padding: 10px;
  margin: 10px 0 10px 0;
  border-radius: 2px;
  outline-style: none;
}

.infofield_btn {
  width: 4.5rem;
  height: 2rem;

  font-family: poppins;
  font-size: 1rem;
  background-color: rgb(235, 232, 232);
  border: 0;
  color: royalblue;
  outline-style: none;
  margin: 10px 10px;
}

.infofield_btn:hover {
  cursor: pointer;
  background-color: rgb(231, 230, 230);
}

.infofield_btn:active {
  background-color: rgb(201, 200, 200);
}
